.game-board {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  height: 500px;
  margin: 0 auto;
}

.game-row {
  display: flex;
  justify-content: center;
  width: 100%;
}


.score-board {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
}

.retry-button {
  display: inline-block;
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
  background-color: white; 
  color: black; 
  border: 2px solid #3cb371;
}

.retry-button:hover {
  background-color: #3cb371;
  color: white;
}
