.cell {
    width: 50px;
    height: 50px;
    border: 2px solid #000;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    background-color: #3cb371;
  }
  
  .cell:hover::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #aaa;
    opacity: 0.8;
  }
  
  .disc {
    width: 80%;
    height: 80%;
    border-radius: 50%;
    transition: background-color 0.5s;
  }
  
  .disc.black {
    background-color: #000;
  }
  
  .disc.white {
    background-color: #fff;
  }

  .hint {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #666;
  }
  
  .cell:not(.empty):hover::before {
    display: none;
  }
  
  @keyframes placeDisc {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .disc {
    width: 80%;
    height: 80%;
    border-radius: 50%;
    transition: background-color 0.5s;
    animation: placeDisc 0.5s;
  }

  .cell.disabled:hover::before {
    display: none;
  }

  .cell:not(.disabled):hover::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #aaa;
    opacity: 0.8;
  }
  